.main-nav{
    min-height: 50px;
    width: 100%;
    margin: 0% auto;
    border-top: solid 3px #ef8236;
    box-shadow: 0px 1px 5px #00000033;
    position: fixed;
    z-index: 5;
    top: 0%;
    left: 0%;
    background-color: #f8f9f9;
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar{
    height: 100%;
    min-height: 80%;
    display: flex;
    align-items:center;
    justify-content:space-between;
    letter-spacing: normal;
    margin-left: 35px; 
    margin-right: 35px;
    width: 100%;
    
}

.navbar .navbar-1 {
    display: flex;
    align-items: center;
    flex-grow: 1;
  }

  .navbar .navbar-2 {
    display: flex;
    align-items: center;
  }

.nav-logo{
    padding: 5px 25px;
}

.nav-item{
    margin: 0px 3px;
    font-size:larger;
    font-weight: 500;
    text-decoration:black;
    color: rgb(69,69,69);
    transition: 0.2s;
}

.nav-btn{
    cursor: pointer;
    border-radius: 20px;
    padding: 10px 20px;
}

.nav-item:hover{
    background-color: rgb(219, 218, 218);
}

.navbar .navbar-1 form{
    flex-grow: 1;
    padding: 0px 12px;
    position: relative;
}

.navbar .navbar-1 form input{
    min-width: 80%;
    margin: 0%;
    padding: 8px 10px 8px 32px;
    font-size: 13px;
    border: solid 1px #0000003e;
    border-radius: 3px;
}

.navbar .navbar-1 form .search-icon{
    position: absolute;
    left: 22px;
    top: 6px;
}

.nav-links{
    padding: 7px 13px;
    border: solid 1px blue;
    border-radius: 3px;
    background-color: #e7f8fe;
    cursor: pointer;
}

.nav-links:hover{
    background-color: rgb(203, 229, 252);
}

.nav-item-nav-links{
    margin-left:10px;
    width:60px;
    height:30px;
    font-size:medium;
    font-weight: 900px;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode',Verdana, sans-serif;
    cursor: pointer;
    background-color: #e7f8fe;
    border: solid 1px blue;
    border-radius: 3px;
}
.nav-item-nav-links:hover{
    background-color: rgb(203, 229, 252);
}


@media screen and (max-width: 1120px) {
    .navabr .navbar-1 form{
        display:none;
    }
}

@media screen and (max-width:6620px) {
    .navbar .navbar-1 .res-nav{
        display:none;
    }
}
