.ask-question {
    min-height: 100vh;
    background-color: #f1f2f3;
}

.ask-ques-container {
    margin: auto;
    padding: 0px 20px 20px 20px;
    max-width: 1200px;
}

.ask-ques-container h1 {
    padding: 60px 0px 20px 0px;
}

.ask-ques-container form .ask-form-container {
    padding: 20px;
    background-color: white;
    border-radius: 3px;
    box-shadow: 0 10px 25px rgb(0 0 0 / 5%), 0 20px 48px rgb(0 0 0 / 5%),
      0 1px 4px rgb(0 0 0 / 10%);
}

.ask-form-container label h4 {
    margin-bottom: 0%;
    font-size:17px;
    margin-top: 8px;
}
  
.ask-form-container label p {
    margin: 0%;
    font-size: 13px;
    padding: 3px 0px;
}
 
.ask-form-container label input,
.ask-form-container label textarea {
  padding: 10px;
  border: solid 1px #0000003e;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  width: calc(100% - 20px);
  resize: vertical;
  font-size: 12px;
  font-weight: 400;
}

.review-btn {
    margin: 40px 0px;
    padding: 10px;
    background-color: #009dff;
    border: solid 1px #009dff;
    color: white;
    border-radius: 4px;
    cursor: pointer;
    transition: 0.3s;
    font-size: 12px;
    font-weight: 600;
}
  .review-btn:hover {
    background-color: #0086d8;
}